<template>
  <section id="how-to-play">
	  <loading :active.sync="isLoading" 
        :can-cancel="true" 
        ></loading>
		<div class=""> 
			<div class="result-card p-5">
				<div class="d-flex justify-content-between align-items-center border-boda pb-3">
					<h3>Current Balance</h3>
					<div class="text-center">
						<h4 class="ml-5">{{userBalance.currency}}<span>{{userBalance.amount}}</span><br></h4> 
						<h6 class="ml-4">Current Balance</h6>
					</div>
				</div>
				<div class="mt-5">
					<p class="p2">Enter Amount</p>
					<input type="text" name="amount" v-model="amount" class="form-control p-4">
					<p class="p2 mt-3">Account Number</p>
					<input type="text" name="amount" v-model="account_number" class="form-control p-4">
					
					 <div class="mt-3 amount">
					  <p class="p2">Pick Your Bank</p>
             <select  title="Pick a number" class="selectpicker form-control" name="paymentMethod" v-model="bank">
			  <option disabled value="">Select Payment Option</option>
              <option value="044">Access Bank</option>
              <option value="023">Citibank Nigeria</option>
			  <option value="063">Diamond Bank</option>
			  <option value="050">Ecobank Nigeria</option>
			  <option value="084">Enterprise Bank</option>
			  <option value="070">Fidelity Bank</option>
			  <option value="011">First Bank of Nigeria</option>
			  <option value="058">Guaranty Trust Bank</option>
			  <option value="030">Heritage Bank</option>
			  <option value="082">Keystone Bank</option>
			  <option value="014">MainStreet Bank</option>
			  <option value="076">Skye Bank</option>
			  <option value="221">Stanbic IBTC Bank</option>
			  <option value="068">Standard Chartered Bank</option>
			  <option value="232">Sterling Bank</option>
			  <option value="032">Union Bank of Nigeria</option>
			  <option value="033">United Bank For Africa</option>
			  <option value="215">Unity Bank</option>
			  <option value="035">Wema Bank</option>
		      <option value="057">Zenith Bank</option>

            </select> 
				  </div>
				  <br>
				</div>
				<span class="max-span  mt-4">*Minimum Widthrawal is N5,000.00</span><br>  
				<button @click.prevent="sendPost()" class="btn play mt-5 py-2 px-5">Widthdraw</button> 
			</div>
		</div>
	</section>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import axios from 'axios';
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";

export default {
    components: { Loading },

	data(){
    return {
      isLoading: false,  
      bank:'',
	  account_number:'',
	  amount:''
    }
	},
	methods: {
	async	sendPost() {
		try {
			 this.showModal = false;
	         this.isLoading = true;
		     let r = Math.random().toString(36).substring(7);
		  
  await axios.post('https://middlewarez.herokuapp.com/api/v1/payment/withdraw', {
    username: this.userDetails.username,
	account_number: this.account_number,
	amount: this.amount,
	reference: r,
	bank_code: this.bank,
	fullname: this.userDetails.firstName + '' + this.userDetails.lastName,
	firstname: this.userDetails.firstName
  })

  setTimeout(() => {
        this.isLoading = false;
	  }, 1000);
					this.$swal({
						type: 'success',
						title: 'Withdrawal Initiated',
						showConfirmButton: false,
						timer: 3000
				})
								this.$router.push('/dashboard')

		} catch (e) {
			this.isLoading = false;
				this.$swal({
					type: 'error',
					title: 'Withdrawal Failed',
					showConfirmButton: false,
					timer: 3000
        })
				return
		}
}
  },
	 computed: {
    ...mapState(["userBalance", "userDetails"])
  }
}
</script>
<style>
.confirmbtnswal{
	background: transparent !important;
	border: none !important;
	outline: none !important;
	color: #269a4c !important;
}
.cancelbtnswal{
	background: transparent !important;
	border: none !important;
	outline: none !important;
	color: #be1b25 !important;
}
</style>
<style scoped> 
#how-to-play {
	background: #fffffe;
	min-height: 700px;
} 
#how-to-play .border-boda {
	border-bottom: 2px solid rgba(20, 0, 5, 0.1)!important;
} 
#how-to-play .p2 {
	font-size: 24px; 
	color: #666666; 
}
#how-to-play .max-span{
	font-size: 18px;
	margin-top: 20px;
}
#how-to-play h6 {
	font-family: Graphik;
	font-size: 20px; 
	color: #666666;
	font-weight:bold;
}
#how-to-play h3 {
	font-size: 30px;
	color: #269A4C;
	font-weight:bold;
} 
#how-to-play h4 {
	font-size: 45px;
	color: #269A4C;
	font-weight:bold;
} 
#how-to-play h4 span{
	font-size: 45px;
	color: #269A4C; 
}

#how-to-play .play {
  background-color: #269A4C; 
  color:#fff; 
	font-size: 24px;
	text-transform: none;
	font-weight:bold; 
	box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.15);
	border-radius: 5px;
}
@media screen and (max-width:1040px) { 
#how-to-play .p2 {
	font-size: 18px;
}
#how-to-play .max-span{
	font-size: 14px;
}
#how-to-play h4 {
	font-size: 20px;
} 
#how-to-play h4 span{
	font-size: 38px;
}
#how-to-play .play {
	font-size: 18px;
}
}
</style>
